<template>
	<view class="content">
		<block v-if="versionStatus">
			<view class="form_box">
				<view class="button">
					<view class="btn" @click="getChooseInvoice()">快速填写开票信息</view>
				</view>
				<view class="label">
					<view class="title"><span>*</span>开票金额</view>
					<view class="input">
						<input type="digit" v-model="formData.tax_money" placeholder="请输入开票金额(元)" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title"><span>*</span>开票名称</view>
					<view class="input">
						<input type="text" v-model="formData.company_name" placeholder="请输入开票名称" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title"><span>*</span>纳税编号</view>
					<view class="input">
						<input type="text" v-model="formData.company_tax" placeholder="请输入纳税编号" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title"><span>*</span>联系邮箱</view>
					<view class="input">
						<input type="text" v-model="formData.user_email" placeholder="请输入联系邮箱(发送发票)" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title">单位地址</view>
					<view class="input">
						<input type="text" v-model="formData.company_address" placeholder="请输入单位地址" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title">单位电话</view>
					<view class="input">
						<input type="text" v-model="formData.company_phone" placeholder="请输入单位电话" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title">开户银行</view>
					<view class="input">
						<input type="text" v-model="formData.bank_name" placeholder="请输入开户银行" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title">银行账号</view>
					<view class="input">
						<input type="number" v-model="formData.bank_number" placeholder="请输入银行账号" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="label">
					<view class="title">联系手机</view>
					<view class="input">
						<input type="text" v-model="formData.user_phone" placeholder="请输入联系手机" placeholder-style="color: #bbb;"/>
					</view>
				</view>
				<view class="button">
					<view class="btn fom" @click="getInvoiceassistantApply()">立即申请开票</view>
				</view>
			</view>
		</block>
		<block v-else>
			<view class="none_list_box">
				<image src="../../static/images/none_01.png" mode="aspectFill"></image>
				<span>开票系统升级中</span>
			</view>
		</block>
		
	</view>
</template>

<script>
	const app = getApp();
	export default {
		data() {
			return {
				versionStatus: __wxConfig.envVersion==='release',
				template: uni.getStorageSync('__ddminiTemplateConfig'),
				formData: {
					company_name: '',
					company_tax: '',
					company_address: '',
					company_phone: '',
					bank_name: '',
					bank_number: '',
					user_phone: '',
					user_email: '',
					remark: '用户申请开票',
					tax_money: ''
				}
			}
		},
		onLoad() {
			let self = this;
			
		},
		methods: {
			getChooseInvoice(){
				let self = this;
				uni.chooseInvoiceTitle({
					success(ces) {
						self.formData.company_name = ces.title;
						self.formData.company_tax = ces.taxNumber;
						self.formData.company_address = ces.companyAddress;
						self.formData.company_phone = ces.telephone;
						self.formData.bank_name = ces.bankName;
						self.formData.bank_number = ces.bankAccount;
					}
				})
			},
			getInvoiceassistantApply(){
				let self = this;
				let data = self.formData;
				if(!data.tax_money){
					return uni.showToast({title: '请输入开票金额', icon: 'none'});
				}else if(!data.company_name){
					return uni.showToast({title: '请输入开票名称', icon: 'none'});
				}else if(!data.company_tax){
					return uni.showToast({title: '请输入纳税编号', icon: 'none'});
				}else if(!data.user_email){
					return uni.showToast({title: '请输入联系邮箱', icon: 'none'});
				}else{
					self.Post(self.Url.invoiceassistantApply, data).then(res => {
						if(res.code === 0){
							uni.showModal({
								content: '申请开票成功，请等待财务审核',
								showCancel: false,
								confirmColor: self.template.color.temp_color,
								success(ces) {
									if(ces.confirm){
										uni.navigateBack();
									}
								}
							})
						}else{
							uni.showModal({
								content: res.msg,
								showCancel: false,
								confirmColor: self.template.color.temp_color
							})
						}
					})
				}
			}
		}
	}
</script>

<style lang="less">
	page{
		background-color: #fff;
	}
	.form_box{
		width: 100%;
		padding-bottom: 50rpx;
		.label{
			width: 90%;
			margin: 0 auto;
			display: flex;
			align-items: center;
			justify-content: space-between;
			border: 1px solid #efefef;
			height: 80rpx;
			border-radius: 100rpx;
			margin-bottom: 20rpx;
			.title{
				width: 200rpx;
				display: flex;
				align-items: center;
				justify-content: center;
				height: 100%;
				color: #666;
				position: relative;
				span{
					color: #2e6cf6;
					position: absolute;
					left: 20rpx;
				}
			}
			.input{
				flex: 1;
				min-height: 0;
				height: 100%;
				display: flex;
				align-items: center;
				input{
					width: 95%;
					height: 80rpx;
					color: #1c1c1c;
					font-size: 28rpx;
				}
			}
		}
	}
	.button{
		width: 90%;
		margin: 0 auto;
		padding-bottom: 20rpx;
		padding-top: 30rpx;
		.btn{
			width: 100%;
			height: 86rpx;
			background-color: #07c160;
			color: #fff;
			font-size: 30rpx;
			display: flex;
			align-items: center;
			justify-content: center;
			border-radius: 100rpx;
		}
		.fom{
			background-color: #2e6cf6;
			color: #fff;
			border-color: #2e6cf6;
		}
	}
</style>
